import React from 'react';
import {createUseStyles} from 'react-jss'

const useStyles = createUseStyles({
    appBar: {
        color: 'white',
        background: 'radial-gradient(ellipse at center, #1e5799 0%,#256ec6 0%,#06307a 100%)',
        borderRadius: 5,
        padding: 10,
        margin: 'auto',
        width: '50%',
        fontSize: 40,
        border: '5px solid white',
        fontFamily: 'Russo One',
        display: 'table'
    }
});

export const AppBar = ({children}) => {
    const classes = useStyles();
    return (
        <span className={classes.appBar}>
            {children}
        </span>
    );
}
