import React from 'react';
import {createUseStyles} from 'react-jss';

const useStyles = createUseStyles({
    link: {
        color: 'white',
        textDecoration: 'none',
        paddingLeft: 20,
        paddingRight: 20,
        paddingTop: 5,
        paddingBottom: 5,
        '&:hover': {
            color: 'yellow'
        }
    }
});

export const MenuLink = ({children, url}) => {
    const classes = useStyles();
    return (
        <a className={classes.link} href={url}>{children}</a>
    );
}
