import React from 'react';
import { AppBar } from './Components/AppBar';
import { MenuItem } from './Components/MenuItem';
import { MenuLink } from './Components/MenuLink';
import { Logo } from './Components/Logo';
import {createUseStyles} from 'react-jss'
import {
  HashRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import { Game } from './Components/Game';

const useStyles = createUseStyles({
  app: {
    color: 'white',
    backgroundImage: `url("/content/images/Stars.png")`,
    height: '100%',
    backgroundColor: 'black'
  },
  body: {
    margin: '0 auto',
    padding: '0 5em',
    maxWidth: '1340px',
    width: '100%',
    height: '100%',
    backgroundColor: 'rgba(50, 50, 50, 0.86)'
  }
});

function App() {
  const classes = useStyles();
  return (
    <Router>
      <div className={classes.app}>
        <div className={classes.body}>
          <Logo/>
          <AppBar>
            <MenuItem url='/'>Home</MenuItem>
            <MenuItem url='/about'>About</MenuItem>
            <MenuItem url='/portfolio'>Portfolio</MenuItem>
            <MenuItem url='/contact'>Contact</MenuItem>
            <MenuLink url='/content/Joshua-Stephens-Resume.pdf'>Resume</MenuLink>
            <MenuLink url='https://www.linkedin.com/in/j-stephens/'>LinkedIn</MenuLink>
          </AppBar>

          <div>
            <Switch>
              <Route path="/about">
                <About />
              </Route>
              <Route path="/portfolio">
                <Portfolio />
              </Route>
              <Route path="/contact">
                <Contact />
              </Route>
              <Route path="/">
                <Home />
              </Route>
            </Switch>
          </div>
        </div>
      </div>
    </Router>
  );
}

export default App;


function Home() {
  return <h2>This website is under development. If you find this, please ignore it.</h2>;
}

function About() {
  return <h2>About</h2>;
}

function Portfolio() {
  return <h2>Portfolio</h2>;
}

function Contact() {
  return <Game url={"/content/games/Tapshot/index.html"}/>;
}