import React from 'react';
import {createUseStyles} from 'react-jss'

const useStyles = createUseStyles({
    game: {
        margin: '0 auto',
        display: 'table',
    },
});

export const Game = ({children, url}) => {
    const classes = useStyles();
    return (
        <div className={classes.game}>
            <iframe title='Resume' src="/content/games/Tapshot/index.html" width="960px" height="640px"/>
        </div>
    );
}
